@import "vars";
@import "libs";
@import "fonts";

::placeholder {
	color: $text; }
html, body {
	height: 100%; }
.main {
	flex: 1 0 auto; }
html {
	font-size: 10px; }
body {
	font-size: 1.6rem;
	min-width: 320px;
	position: relative;
	line-height: 1.6;
	font-family: $default-font;
	overflow-x: hidden;
	color: $text;
	input, textarea {
		display: block;
		width: 100%;
		outline: none;
		resize: none;
		border: none; }
	input, textarea, button, a {
		&:active, &:hover, &:focus {
			outline: 0;
			outline-offset: 0; } }
	button {
		cursor: pointer;
		background-color: transparent;
		border: none;
		padding: 0; } }
b, strong {
	font-weight: 700; }
p {
	margin-bottom: 0; }
a {
	color: $text;
	text-decoration: none;
	transition: 0.25s;
	&:hover {
		color: inherit;
		text-decoration: underline; }
	&[href^="#"], &[href^="tel"], &[href^="mail"] {
		&:hover {
			text-decoration: none; } }
	&.link-decorated {
		text-decoration: underline;
		font-size: 1.7rem;
		&:hover {
			text-decoration: none; } } }
.link-more {
	font-size: 1.8rem;
	color: $blue;
	&:after {
		content: '';
		@include size(1.7rem, 1.3rem);
		background: url(../img/icon/icon-arrow.png) center no-repeat;
		display: inline-block;
		margin-left: 1rem;
		transition: 0.25s; }
	&:hover {
		text-decoration: none;
		color: $blue;
		&:after {
			transform: translateX(.5rem); } } }
.w-100 {
	width: 100%; }
.h-100 {
	height: 100%; }
.list {
	&-unstyled {
		@include list-unstyled; } }
.icon {
	display: inline-block;
	vertical-align: middle;
	top: -0.3rem;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	&-left {
		margin-right: 1rem; }
	&-right {
		margin-left: .8rem; }
	&-phone {
		@include size(1.6rem, 1.7rem);
		background-image: url(../img/icon/icon-phone.png); }
	&-mail {
		@include size(2.5rem, 1.8rem);
		background-image: url(../img/icon/icon-mail.png); }
	&-search {
		@include size(1.6rem, 1.6rem);
		background-image: url(../img/icon/icon-search.png); } }
.image {
	display: block;
	background-position: center;
	background-repeat: no-repeat;
	position: relative;
	&-con {
		background-size: contain; }
	&-cov {
		background-size: cover; }
	&-hover {
		overflow: hidden;
		cursor: pointer;
		&:before {
			content: '';
			@include size(100%);
			background: inherit;
			position: absolute;
			top: 0;
			left: 0;
			transition: 0.4s; }
		&:hover:before {
			transform: scale(1.05, 1.05); } } }
.text {
	&-xs {}
	&-s {}
	&-m {}
	&-l {}
	&-xl {}
	&-blue {
		color: $blue; } }
.btn {
	display: flex;
	cursor: pointer;
	transition: 0.25s;
	justify-content: center;
	align-items: center;
	text-decoration: none!important;
	position: relative;
	z-index: 1;
	&-l {
		height: 6rem;
		font-size: 1.8rem;
		font-weight: 600;
		letter-spacing: 0.018rem;
		padding: 0 4.6rem;
		min-width: 25rem; }
	&-blue {
		color: #ffffff;
		&:before {
			background-color: $accent; }
		&:after {
			background-color: #4555b9; }
		&:hover {
			color: $text; } }
	&-yel {
		color: $text;
		&:before {
			background-color: #4555b9; }
		&:after {
			background-color: $accent; }
		&:hover {
			color: #fff; } }
	&-blue, &-yel {
		&:before {
			position: absolute;
			content: '';
			@include size(0%,100%);
			top: 0;
			left: auto;
			right: 0;
			z-index: -1;
			transition: all 300ms ease; }
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			@include size(100%);
			z-index: -2; }
		&:hover {
			&:before {
				position: absolute;
				content: '';
				@include size(100%);
				top: 0;
				left: 0;
				right: 0;
				z-index: -1; } } } }
.hidden {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	pointer-events: none; }
.row {
	&-xs {
		@include make-row(1rem);
		> * {
			padding: {
				left: .5rem;
				right: .5rem; } } }
	&-m {
		@include make-row(4.5rem);
		> * {
			padding: {
				left: 2.25rem;
				right: 2.25rem; } } }
	&-l {
		@include make-row(8rem);
		> * {
			padding: {
				left: 4rem;
				right: 4rem; } } }
	&-xl {
		@include make-row(10rem);
		> * {
			padding: {
				left: 5rem;
				right: 5rem; } } } }
.form {
	display: block;
	&-group {
		position: relative;
		&:not(:first-child) {
			margin-top: 3.1rem; } }
	&-submit {
		margin-top: 4rem;
		.btn {
			width: 100%;
			margin: 0 auto;
			padding: 0 2rem; } }
	&-control {
		border-bottom: .2rem solid rgba(#7b7b7b, .3);
		height: 3rem;
		line-height: 2.8rem;
		padding: 0;
		color: $text;
		font-size: 1.6rem;
		background-color: transparent; }
	&-checkbox {
		@extend .hidden;
		+ .form-label {
			cursor: pointer;
			padding-left: 3rem;
			position: relative;
			margin-bottom: 0;
			display: block;
			&:before, &:after {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0; }
			&:before {
				content: '';
				@include size(1.4rem);
				border: 0.1rem solid #000000; }
			&:after {
				content: '\2714';
				@include size(1.4rem);
				line-height: 1.4rem;
				text-align: center;
				opacity: 0;
				transition: .25s; } }
		&:checked + .form-label:after {
			opacity: 1; } }
	&-radio {
		@extend .hidden;
		+ .form-label {
			cursor: pointer;
			padding-left: 3rem;
			position: relative;
			margin-bottom: 0;
			display: block;
			&:before, &:after {
				content: '';
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				border-radius: 100%; }
			&:before {
				@include size(1.4rem);
				border: 0.1rem solid #000000;
				left: 0; }
			&:after {
				@include size(.6rem);
				opacity: 0;
				transition: .25s;
				background-color: #000000;
				left: .4rem; } }
		&:checked + .form-label:after {
			opacity: 1; } }
	&-promo {
		border: 0.6rem solid $accent;
		background-color: rgba(#ffffff,.9);
		width: 39.8rem;
		padding: 3rem 6.9rem 2.8rem; }
	&-title {
		font-size: 2.4rem;
		font-weight: 600;
		text-transform: uppercase;
		margin-bottom: .6rem; }
	&-subtitle {
		font-weight: 500;
		margin-bottom: 4.3rem;
		font-size: 1.8rem;
		letter-spacing: 0.018rem; }
	&-title, &-subtitle {
		display: block;
		text-align: center;
		color: $text;
		line-height: 1.25; } }
.section {
	&-title {
		background: url(../img/data/title.png) center repeat-x;
		text-align: center;
		text-transform: uppercase;
		margin-bottom: 8.9rem;
		display: flex;
		justify-content: center;
		> * {
			margin-bottom: 0;
			padding: {
				left: 12rem;
				right: 12rem; }
			background-color: #ffffff; } } }

// elements
.wrapper {
	box-shadow: 0 0 2.3rem 0.6rem rgba(0, 0, 0, 0.22);
	display: flex;
	flex-direction: column;
	max-width: 1400px;
	width: 100%;
	margin: 0 auto;
	min-height: 100vh;
	position: relative; }
// elements

// header
.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 10;
	color: #ffffff;
	padding: {
		top: 2rem; }
	&-menu {
		&_list {
			display: flex;
			justify-content: flex-end;
			> li {
				position: relative;
				&.menu-item-has-children {
					&:after {
						content: '';
						position: absolute;
						top: 100%;
						left: 0;
						background: url(../img/data/dropdown.png) right top no-repeat;
						@include size(calc(100% + 2.5rem), .8rem);
						transition: .25s;
						opacity: 0;
						visibility: hidden; }
					& > a {
						position: relative;
						&:before, &:after {
							content: '';
							position: absolute;
							@include size(1.6rem, 100%);
							background-position: right center;
							background-repeat: no-repeat;
							display: block;
							left: 100%;
							top: 0;
							transition: .25s; }
						&:before {
							background-image: url(../img/icon/icon-dropdown.png); }
						&:after {
							background-image: url(../img/icon/icon-dropdown_active.png);
							opacity: 0;
							visibility: hidden; } }
					&:hover {
						&:after {
							opacity: 1;
							visibility: visible; }
						> a {
							&:before {
								opacity: 0;
								visibility: hidden; }
							&:after {
								opacity: 1;
								visibility: visible; } } } }
				&:not(:first-child) {
					margin-left: 3.7vw; }
				> a {
					font-weight: 600;
					display: block; } }
			li:hover {
				> a {
					color: #e6ac0c;
					text-decoration: none; }
				> ul {
					opacity: 1;
					pointer-events: auto;
					visibility: visible; } }
			a {
				color: #ffffff; }
			ul {
				opacity: 0;
				pointer-events: none;
				visibility: hidden;
				transition: 0.25s;
				position: absolute;
				@include list-unstyled();
				white-space: nowrap;
				background-color: #272c4c;
				padding: 1.8rem 3rem 3rem;
				top: calc(100% + .8rem);
				li {
					border-bottom: .1rem solid #3d3d3d;
					&:not(:first-child) {
						margin-top: .9rem; }
					a {
						font-weight: 500;
						font-size: 1.4rem;
						display: block; } } } } }
	&-lang, &-contact {
		color: rgba(#fff,.4);
		&_link {
			color: rgba(#fff,.4);
			font-size: 1.4rem; } }
	&-lang {
		&_link {
			&.is-active, &:hover {
				color: #5064de;
				text-decoration: none; } }
		&_item:not(:first-child) {
			margin-left: 0.5rem;
			&:before {
				content: '/';
				color: rgba(#fff,.4);
				font-size: 1.4rem;
				margin-right: 0.3rem; } } }
	&-scroll {
		position: fixed;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		background-color: #ffffff;
		color: $text;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		transition: 0.4s;
		max-width: 1400px;
		width: 100%;
		z-index: 20;
		border-bottom: .1rem solid $accent;
		padding: {
			top: 2.1rem;
			bottom: 1.4rem; }
		.header {
			&-menu_list {
				> li {
					> a {
						color: $text; }
					&:not(:first-child) {
						margin-left: 4rem; }
					&.menu-item-has-children {
						> a {
							&:before {
								background-image: url(../img/icon/icon-dropdown_b.png); } } } }
				> li:hover {
					> a {
						color: #e6ac0c; } } }
			&-contact {
				color: $text;
				&_link {
					color: $text; } } } } }
.scroll {
	.header-scroll {
		opacity: 1;
		visibility: visible;
		pointer-events: auto; } }
// header

// promo
.promo {
	height: 100vh;
	max-height: 82.7rem;
	position: relative;
	color: #fefefe;
	background-color: #000000;
	margin-bottom: 8.9rem;
	&-slider {
		height: 100%;
		.owl-carousel {
			height: 100%;
			*:not(.h1) {
				height: 100%; } }
		&_block {
			display: flex;
			align-items: flex-end;
			padding-bottom: 11rem;
			.h1 {
				max-width: 54rem;
				line-height: 1.67;
				letter-spacing: 0.036rem;
				margin-bottom: 0;
				text-shadow: 0 0.1rem 0.7rem rgba(0, 0, 0, 0.26); } } }
	&-form {
		position: absolute;
		bottom: 9rem;
		left: 0;
		right: 0;
		pointer-events: none;
		z-index: 10;
		.form-promo {
			pointer-events: auto; } } }
.slider-control {
	position: absolute;
	left: 0;
	bottom: 12.5rem;
	z-index: 10;
	overflow: hidden;
	&_link {
		color: rgba(#ffffff, .3);
		display: block;
		position: relative;
		font-size: 2rem;
		transition: 0.25s;
		cursor: pointer;
		font-weight: 500;
		padding: {
			left: 1.8rem;
			bottom: 0.4rem; }
		line-height: 1.2;
		&:not(:first-child) {
			margin-top: 3.1rem; }
		&:after {
			content: '';
			display: block;
			@include size(100%, .4rem);
			transform: translateX(-100%);
			background-color: $accent;
			transition: 0.25s;
			position: absolute;
			left: 0;
			bottom: 0; }
		&.is-active {
			font-weight: 700;
			&:after {
				transform: translateX(0rem); } }
		&.is-active, &:hover {
			color: #ffffff; } } }
// promo


// services
.services {
	margin-bottom: 16.4rem;
	&-title {
		position: relative;
		padding-left: 3.9rem;
		margin-bottom: 3.4rem;
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			@include size(.6rem, 100%);
			background-color: $accent; } }
	&-text {
		max-width: 65rem;
		margin-bottom: 4.3rem;
		p {
			padding-left: 3.9rem;
			line-height: 1.5; } }
	&-block {
		margin-bottom: 14.3rem;
		&:nth-child(2n) {
			flex-direction: row-reverse;
			.services-block_content {
				margin-right: 0;
				margin-left: 11.9rem; } }
		&_image {
			width: 47.9rem;
			.image {
				margin-left: 4.3rem;
				padding-bottom: 76%;
				&:before, &:after {
					content: '';
					display: block;
					background-color: $accent;
					position: absolute;
					left: -4.3rem; }
				&:before {
					@include size(4.3rem, .6rem);
					top: 6.8rem; }
				&:after {
					width: .6rem;
					top: 7.4rem;
					bottom: 0; } } }
		&_text {
			border: .6rem solid $accent;
			border-top: none;
			padding: 4.1rem 3.8rem 6.6rem;
			margin-right: 8.1rem;
			.h5 {
				margin-bottom: 2.8rem; }
			p {
				line-height: 1.5; } }
		&_content {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			padding-left: 3.9rem!important;
			margin-right: 11.9rem;
			.services-text {
				margin-left: -3.9rem;
				max-width: none;
				p {
					margin-top: 4.2rem; } }
			.services-title {
				margin-bottom: 0; }
			img {
				@include img-fluid;
				margin-bottom: 2.6rem; }
			p {
				line-height: 1.5; } }
		&_01 {
			flex-direction: row-reverse;
			.services-block_content {
				margin-right: 0;
				margin-left: 11.7rem;
				padding-left: 0!important;
				.image {
					@include size(8.4rem);
					margin-bottom: 2.6rem; }
				.h4 {
					color: $blue; } } }
		&_02, &_04 {
			.services-block_content {
				.image {
					padding-bottom: 100%;
					position: relative;
					margin-bottom: 1.6rem;
					&:after {
						content: '';
						position: absolute;
						top: 0;
						left: 0;
						right: 0;
						bottom: 0;
						background: url(../img/data/service-block_02.png) center no-repeat;
						background-size: cover; }
					+ .h5 {
						margin-bottom: 0;
						&:after {
							content: '';
							@include size(8.4rem ,0.1rem);
							background-color: rgba(#000, .15);
							display: block;
							margin: .8rem auto .5rem; } } } } }
		&_04 {
			.services-block_content {
				.image {
					&:after {
						display: none; }
					&.image-con {
						padding-bottom: 70%; } } } } } }
// services


// productions
.productions {
	margin-bottom: 15.5rem;
	&-box {
		margin-bottom: -2rem; }
	&-block {
		margin-bottom: 8rem;
		.image {
			padding-bottom: 83%;
			position: relative;
			margin-bottom: 2.5rem;
			&:after {
				content: '';
				display: block;
				position: absolute;
				z-index: -1;
				top: 0.6rem;
				left: 0.6rem;
				right: -0.6rem;
				bottom: -0.6rem;
				background-color: $accent; } }
		&:nth-child(even) {
			.image {
				&:after {
					top: -.6rem;
					left: -.6rem;
					bottom: .6rem;
					right: .6rem; } } }
		.link-more {
			margin-bottom: 0; } } }
// productions


// news
.news {
	margin-bottom: 11rem;
	&-box {
		margin-bottom: -3rem; }
	&-block {
		color: $blue;
		margin-bottom: 3rem;
		.image {
			padding-bottom: 82%;
			margin-bottom: 4.1rem; }
		&_title {
			position: relative;
			padding-left: 3.9rem;
			line-height: 1.25;
			margin-bottom: 2.6rem;
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				top: 0;
				@include size(.6rem, 100%);
				background-color: $accent; } }
		&_date {
			display: block;
			margin-bottom: 1.8rem; }
		&_date, p {
			padding-left: 3.9rem; }
		p {
			line-height: 1.67;
			font-size: 1.8rem; } } }
// news

// faq
.faq {
	margin-bottom: 12.3rem;
	&-block {
		margin-bottom: 4rem;
		&_control {
			display: flex;
			background-color: #f4f4f4;
			min-height: 12.8rem;
			align-items: center;
			font-size: 2rem;
			padding: {
				left: 5.1rem;
				right: 7.5rem; }
			font-weight: 600;
			cursor: pointer;
			position: relative;
			line-height: 1.2;
			&:after {
				content: '';
				@include size(1.6rem, .9rem);
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 4.5rem;
				background: url(../img/data/arrow-l.png) center no-repeat; } }
		&_content {
			display: none;
			background-color: #f4f4f4;
			padding: 0 5.1rem 3rem;
			p {
				line-height: 1.5; } } } }
// faq

// order
.order {
	&-list {
		margin-top: 4rem;
		&_item {
			position: relative;
			display: flex;
			min-height: 3.9rem;
			align-items: center;
			padding-left: 6.9rem;
			&:before {
				content: '';
				@include size(3.9rem,3.9rem);
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				background: url(../img/data/order-item.png) center no-repeat; }
			&:not(:first-child) {
				margin-top: 3.2rem; } } } }

// order

// footer
.footer {
	margin-top: 15.4rem;
	background-image: linear-gradient(to top, #353a59 0%, #272c4c 50%, #353a59 100%);
	color: #fefefe;
	padding: {
		top: 6.5rem;
		bottom: 3.8rem; }
	a {
		color: #fefefe; }
	&-copy {
		display: block;
		margin-top: 0.8rem;
		font-size: 1.4rem;
		line-height: 2; }
	&-list {
		padding-top: .5rem;
		> li > a {
			font-weight: 700; }
		ul {
			margin-top: 1.1rem;
			> li:not(:first-child) {
				margin-top: .4rem; }
			a {
				font-size: 1.4rem; } } }
	&-contact {
		margin-top: 1.1rem;
		li:not(:first-child) {
			margin-top: .2rem; }
		a {
			font-size: 1.4rem; } }
	&-social {
		margin-top: 2rem; } }
// footer
@import "media";
