@import "vars";


/*==========  Desktop First  ==========*/
@media screen and (max-width: 1320px) {
	.row {
		&-m, &-l, &-xl {
			@include make-row(2.4rem);
			> * {
				padding: {
					left: 1.2rem;
					right: 1.2rem; } } } } }
// ≥1450px
@include media-breakpoint-down(xl) {
	.promo-slider_block {
		padding-left: 3.9rem; }
	.header-scroll {
		left: 0;
		right: 0;
		transform: translateX(0); } }
// ≥1200px
@include media-breakpoint-down(lg) {
	.section-title {
		margin-bottom: 5rem; }
	.promo-slider_block .h1 {
		max-width: 50%; }
	.header-scroll {
		.header-menu {
			position: absolute;
			top: 100%;
			background-color: #ffffff;
			opacity: 0;
			visibility: hidden;
			pointer-events: none; } }
	.services {
		&-block {
			margin-bottom: 7rem;
			&:nth-child(2n) {
				.services-block_content {
					margin-left: 4rem; } }
			&_content {
				margin-right: 4rem; }
			&_01 {
				.services-block_content {
					margin-left: 4rem; } } } }
	.services, .productions, .news, .faq {
		margin-bottom: 9rem; } }
// ≥992px
@include media-breakpoint-down(md) {
	.promo-slider_block .h1 {
		max-width: none; }
	.section-title {
		> * {
			padding: {
				left: 3rem;
				right: 3rem; } } }
	.services-block_image {
		width: 100%;
		margin-bottom: 3rem;
		.image {
			padding-bottom: 50%; } }
	.services-block_text {
		padding-bottom: 3.8rem; }
	.footer {
		margin-top: 5rem;
		padding: {
			top: 3rem;
			bottom: 3rem; }
		&-logo {
			text-align: center;
			margin-bottom: 2rem; } } }
// ≥768px
@include media-breakpoint-down(sm) {
	.header:not(.header-scroll) {
		padding-top: 1rem;
		.header-logo {
			img {
				max-width: 118px; } }
		.container {
			> .row-m:first-child {
				margin-bottom: 1.5rem; } }
		.hamburger-inner, .hamburger-inner::after, .hamburger-inner::before {
			background-color: #ffffff; }
		.header-menu {
			position: absolute;
			top: 100%;
			background-color: #ffffff;
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			left: 0;
			right: 0; } }
	.news-block {
		p {
			line-height: 1.5;
			font-size: 1.6rem; } }
	.footer-list {
		margin-bottom: 2rem; }
	.header-contact {
		.icon {
			display: none; } } }
// ≥576px
@include media-breakpoint-down(xs) {
	.header-scroll {
		padding: {
			top: 1rem;
			bottom: 1rem; } }
	.promo-slider_block .h1 {
		line-height: 1.2; }
	.services-title {
		padding-left: 1.9rem; }
	.services-text p {
		padding-left: 1.9rem; }
	.services-block_image .image {
		margin-left: 0;
		&:before, &:after {
			display: none; } }
	.services-block_text {
		padding: 2rem 1.3rem;
		margin-right: 0; }
	.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
		margin-bottom: 1.5rem; }
	.services-block_text .h5 {
		margin-bottom: 1.5rem; }
	.services-block_01 .services-block_content {
		.image {
			margin: {
				left: auto;
				right: auto; } }
		.h4 {
			text-align: center; } }
	.services-block_content {
		padding-left: 1.9rem!important; }
	.services-block_content .services-text {
		margin-left: -1.9rem; }
	.productions-block {
		margin-bottom: 3rem; }
	.form-promo {
		width: 100%;
		padding: 3rem 2rem; } }
// ≥410px
@include media-breakpoint-down(xxs) {
	h1, .h1 {
		font-size: 3.2rem; } }

/*==========  Mobile First  ==========*/

// ≤576px
@include media-breakpoint-up(sm);

// ≤768px
@include media-breakpoint-up(md);

// ≤992px
@include media-breakpoint-up(lg);

// ≤1200px
@include media-breakpoint-up(xl);
