$default-font: 'AvenirNextCyr', sans-serif;
$accent: #ffba00;
$link: #4454b8;
$blue: #4453b8;
$text: #292929;

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	display: block;
	margin-bottom: 3rem;
	line-height: 1.2;
	font-weight: 600; }
h1, .h1 {
	font-size: 3.6rem; }
h2, .h2 {
	font-size: 3rem; }
h3, .h3 {
	font-size: 2.4rem; }
h4, .h4 {
	font-size: 2.2rem; }
h5, .h5 {
	font-size: 2rem; }
h6, .h6 {
	font-size: 1.8rem; }

// Customization Bootstrap vars from "app/libs/bootstrap/scss/_variables.scss"
$grid-gutter-width: 2.4rem;
$grid-columns: 12;
$grid-breakpoints: (xxs: 0, xs: 415px, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1450px);
$container-max-widths: (xs: 380px,sm: 540px, md: 720px, lg: 960px, xl: 1170px, xxl: 1264px);

$gutter: $grid-gutter-width;
